/* open-sans-regular - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: local("Open Sans Regular"), local("OpenSans-Regular"),
    url("./fonts/open-sans-v18-latin_cyrillic-regular.woff2") format("woff2"),
    /* Chrome 26+, Opera 23+, Firefox 39+ */
      url("./fonts/open-sans-v18-latin_cyrillic-regular.woff") format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* open-sans-600 - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  src: local("Open Sans SemiBold"), local("OpenSans-SemiBold"),
    url("./fonts/open-sans-v18-latin_cyrillic-600.woff2") format("woff2"),
    /* Chrome 26+, Opera 23+, Firefox 39+ */
      url("./fonts/open-sans-v18-latin_cyrillic-600.woff") format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

*,
*:before,
*::after {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
button {
  display: block;
}
body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
}
.main {
  max-width: 1365px;
  background: url("./img/Rectangle1.png") no-repeat top right;
  color: #ffffff;
  margin: auto;
}
.wrapper {
  max-width: 1308px;
  padding: 2vw 2vw;
  margin: auto;
}
.top {
  display: flex;
  justify-content: flex-end;
  line-height: 40px;
  margin-bottom: 17px;
}
.top__bell {
  max-width: 24px;
  height: 30px;
  margin-right: 20px;
}
.vl {
  border-left: 1px solid white;
  height: 39px;
}
.top__avatar {
  max-width: 40px;
  border-radius: 50%;
  margin: 0 20px;
}
.title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 10px;
}
.subtitle {
  margin-bottom: 32px;
}
.user {
  display: flex;
  justify-content: space-between;
  background: #1a78c2;
  font-weight: 600;
  line-height: 10vw;
  padding: 0 2vw;
  border-radius: 10px;
  margin-bottom: 24px;
}
.user__name,
.user__edit,
.info__email,
.info__phone,
.new-info__form {
  display: flex;
}
.user__name__avatar {
  border-radius: 50%;
  align-self: center;
  margin-right: 42px;
}
.user__name__text {
  font-size: 30px;
}
.user__edit {
  align-self: center;
}
.user__edit__text {
  color: white;
  font-size: 14px;
  margin-right: 8px;
}
.user__edit__img {
  align-self: center;
}
.close {
  color: white;
  text-decoration: none;
}
.info {
  font-size: 18px;
  color: #313131;
  border-radius: 10px;
  background: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.15), 0 6px 20px 0 rgba(0, 0, 0, 0.15);
}
.info__email {
  justify-content: flex-start;
  padding: 33px 0;
  border-bottom: 1px solid #cae7fe;
}
.info__at {
  max-width: 30px;
  margin: 0 45px 0 77px;
}
.info__phone {
  justify-content: flex-start;
  padding: 34px 0;
}
.info__phone__img {
  max-width: 28px;
  margin: 0 45px 0 78px;
}
.new-info {
  background: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.15), 0 6px 20px 0 rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 49px 0 44px;
}
.new-info__info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding-bottom: 46px;
}
.new-info__form {
  margin-right: 2vw;
}
.new-info__img {
  max-height: 30px;
  /* align-self: center; */
  margin: 25px 20px 0 0;
}
ul {
  padding: 0;
  margin: 0;
}
fieldset {
  width: 278px;
  height: 71px;
  border: 1px solid rgba(49, 49, 49, 0.4);
  border-radius: 5px;
}
.feildset__error {
  border-color: red;
}
legend {
  font-size: 12px;
  color: rgba(49, 49, 49, 0.7);
}
.legend__error {
  color: red;
}
input {
  width: 100%;
  height: 100%;
  color: rgba(49, 49, 49, 0.5);
  border: none;
  padding-left: 10px;
}
.vel {
  border-left: 1px solid #cae7fe;
  height: 7vw;
}

.new-info__button {
  width: 212px;
  height: 49px;
  border-radius: 25px;
  background: #01bda7;
  color: white;
  border: none;
  margin: auto;
}
.Modal {
  position: absolute;
  top: 40px;
  left: 40px;
  right: 40px;
  bottom: 40px;
  border: 1px solid #ccc;
  background: #fff;
  overflow: auto;
  outline: none;
  padding: 20px;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  width: 600px;
  height: 318px;
}
.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.2);
}
.modal__close {
  position: absolute;
  top: 20px;
  right: 20px;
  border: none;
  background: white;
}
.modal__text {
  color: rgba(49, 49, 49, 0.7);
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 30px;
}
.modal__button {
  width: 202px;
  height: 50px;
  border-radius: 25px;
  background: #01bda7;
  color: white;
  border: none;
  margin-bottom: 28px;
}
.modal__button_white {
  width: 202px;
  height: 50px;
  border-radius: 25px;
  background: white;
  color: #01bda7;
  border-color: #01bda7;
}
@media only screen and (max-width: 1220px) {
  .vel {
    display: none;
  }
}

@media only screen and (max-width: 700px) {
  .user__name__avatar {
    max-width: 50px;
  }
  .close {
    margin-right: 15px;
  }
.close span {
  display: none;
}
}
@media only screen and (max-width: 600px) {
  .user__edit__text {
    display: none;
  }
  .user__edit__img {
    padding-top: 5vw;
    padding-right: 2vw;
  }
}
@media only screen and (max-width: 460px) {
  .top {
    padding-top: 10px;
  }
  .top__name {
    display: none;
  }
  .top__bell {
    max-width: 16px;
    height: 24px;
    margin-right: 14px;
  }
  .vl {
    height: 24px;
  }
  .top__avatar {
    max-width: 24px;
    margin: 0 10px;
  }
  .title {
    font-size: 14px;
  }
  .subtitle {
    font-size: 12px;
    margin-bottom: 10px;
  }
  .user {
    height: 71px;
    margin-bottom: 10px;

  }
  .user__name__text {
    font-size: 14px;
    align-self: center;
  }
  .user__name__avatar {
    width: 40px;
    margin-right: 10px;
  }
  .info__at {
    width: 20px;
height: 20px;
    margin: 0 12px 0 12px;
  }
  .info__phone, .info__email {
    font-size: 14px;
  }
  .info__phone__img {
    width: 18px;
    height: 18px;
    margin: 0 13px 0 13px;
  }
  .new-info__img {
    display: none;
  }
  .new-info {
    border-radius: 10px;
    padding: 7px 0;
  }
  .new-info__info {
    padding-bottom: 7px;
  }
  .new-info__button {
   margin-bottom: 17px;
  }
.close {
  margin-top: 20px;
  margin-right: 15px;
}
.Modal {
width: 320px;
height: 426px;
left: 0px;
right: 0px;
top: 180px;
}
.modal__text {
  font-size: 18px;
}
}
